body{
    background-color: #9266ff;
  }
  
  .App{
    background-color: #3f3160;
    min-height:500px;
    width: 80%;
    margin: 40px auto;
    border-radius: 10px;
  }
  
  #blog-entry-form input{
    background-color: rgba(27, 112, 137, 1);
    border: 0; 
    color: #fff;
    width: 30%;
    height: 40px;
    margin: 20px;
    padding: 0 20px;
    font-size: 14px;
    border-radius: 10px;
  }
  
  #blog-entry-form input::placeholder{
    color: rgba(255,255,255,0.5);
  }
  
  #blog-entry-form input:focus{
    outline: none;
  }
  
  #blog-entry-form button{
    height: 40px;
    width: 60px;
    border-radius: 5px;
    border: 0;
    background-color:rgb(211, 162, 26); 
    cursor: pointer;
    color: #fff;
    font-weight: bolder;
  
  }
  
  #blog-entry-form button:focus{
    outline: none;
  }
  
  h1 {
    color: white;
    text-align: center;
    font-size: 20px;
    margin-top: 10%;
  }
  
  .footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    color: white;
    text-align: center;
  }
  
  .timestamp {
    font-size: 9px;
    margin-left: 27%
  }
  
  /* larger screens */
  
  @media screen and (min-width: 768px) {
      .App{
        min-height: 300px;
        width: 60%;
      }
  
      #blog-entry-form input{
        width: 60%;
      }
  
      h1 {
        color: white;
        font-size: 26px;
        text-align: left;
        margin-left: 25%;
        margin-top: 5%;
  
      }
  
      .timestamp{
        font-size: 12px;
        margin-left: 24%;
      }
  
  
  }